import { useState } from "react";
const Products = ({ product }) => {
    return (
        <div>
            <p>{product.name}: {product.amount}kpl</p>
            <img src={product.image} alt="kuva" className="ProductPic" />
        </div>
    )
}

const Confirm = ({ products, setPage, setProducts }) => {
    const [newPurchase, setNewPurchase] = useState({ nimi: "", email: "", numero: "", osoite: "", postinumero: "", postitoimipaikka: "" });
    const [confirm, setConfirm] = useState(0);

    const filteredProducts = products.filter(c => c.amount > 0)

    const purchaseHandler = (e, field) => {
        const tempPurhcase = { ...newPurchase };
        tempPurhcase[field] = e.target.value;
        setNewPurchase(tempPurhcase);
    }

    const setToZero = () => {
        const tempCart = products.map(l => {
            l = { ...l, amount: 0 };
            return l;
        });
        setProducts(tempCart);
    }

    return (
        <div>
            {confirm === 0 &&
                <div>
                    <form>
                        <input type="text" placeholder="Nimi" value={newPurchase.nimi} onChange={e => purchaseHandler(e, 'nimi')} required />
                        <input type="text" placeholder="Sähköposti" value={newPurchase.email} onChange={e => purchaseHandler(e, 'email')} required />
                        <input type="text" placeholder="Puhelinnumero" value={newPurchase.numero} onChange={e => purchaseHandler(e, 'numero')} required /><br />
                        <input type="text" placeholder="Osoite" value={newPurchase.osoite} onChange={e => purchaseHandler(e, 'osoite')} required />
                        <input type="text" placeholder="Postinumero" value={newPurchase.postinumero} onChange={e => purchaseHandler(e, 'postinumero')} required />
                        <input type="text" placeholder="Postitoimipaikka" value={newPurchase.postitoimipaikka} onChange={e => purchaseHandler(e, 'postitoimipaikka')} required /><br />
                        <button onClick={() => setConfirm(confirm + 1)}>Lähetä</button>
                    </form>
                    {filteredProducts.map(c => <Products key={c.id} product={c} />)}
                </div>}
            {confirm === 1 &&
                <div>
                    <h2>Ostos on vahivistettu</h2>
                    <h3>Tuotteet on lähetetty osoitteeseen {newPurchase.osoite}, {newPurchase.postitoimipaikka}</h3>
                    <button onClick={() => setPage(1) + setToZero()}>OK</button>
                </div>
            }
        </div>
    )
}
export default Confirm;