import "../App.css";

const Product = ({ product, addToCart, removeFromCart }) => {
    return (
        <div className="products">
            <p>
                {product.name} <br />
                {product.hinta}€
            </p>
            <img src={product.image} alt="kuva" className="ProductPic" />
            <br />
            <button className="add" onClick={() => addToCart(product.id)}>
                Lisää ostoskoriin
            </button> <br/>
            <button className="add" onClick={() => removeFromCart(product.id)}>
                Poista ostoskorista
            </button>
            <p>ostoskori: {product.amount}</p>
        </div>
    );
};

const Products = ({ products, setProducts }) => {
    const addToCart = id => {
        const tempProducts = products.map(l => {
            if (id === l.id) {
                return { ...l, amount: l.amount + 1 };
            } else return l;
        })
        setProducts(tempProducts);
    }
    const removeFromCart = id => {
        const tempProducts = products.map(l => {
            if (id === l.id && l.amount > 0) {
                return { ...l, amount: l.amount - 1 };
            } else return l;
        })
        setProducts(tempProducts);
    }
    return (
        <div>
            {products.map(p =>
                <Product key={p.id} product={p} addToCart={addToCart} removeFromCart={removeFromCart} />
            )}
        </div>
    );
};
export default Products;
