import { useState } from "react";
import "./App.css";
import Products from "./components/Product";
import productData from "./components/ProductData";
import { Cart } from './components/Cart';
import Confirm from './components/Confirm';

function App() {
  const [products, setProduct] = useState(productData);
  const [page, setPage] = useState(1);
  return (
    <div className="App">
      <header className="App-header">
        <h1>Fanikauppa</h1>
      </header>
      {(page === 1) && <div><div className="products block">
        <h2>Tuotteet</h2>
        <Products products={products} setProducts={setProduct} />
      </div>
        <div className="ostoskori">
          <h2>Ostoskori</h2>
          <Cart products={products} setProducts={setProduct} setPage={setPage}/>
        </div></div>}
      {page === 2 && <div className="form">
        <Confirm products={products} setProducts={setProduct} setPage={setPage}/>
      </div>}
    </div>
  );
}

export default App;