const Purchase = ({ product }) => {
    return (
        <div>
                <p>{product.name}: {product.amount}kpl</p>
        </div>
    )
}

const Cart = ({ products, setProducts, setPage }) => {
    const calcTotal = () => {
        const price = products.map(l => l.hinta * l.amount);
        return price.reduce((a, b) => Math.round((a + b + Number.EPSILON) * 100) / 100)
    }
    const setToZero = () => {
        const tempProducts = products.map(l => {
            l = { ...l, amount: 0 };
            return l;
        });
        setProducts(tempProducts);
    }
    const filteredProducts = products.filter(c => c.amount > 0)
    return (
        <div className="ostoskori">
            <button className="ostoskoriButton" onClick={() => setToZero()}>Tyhjennä</button>
            <button className="ostoskoriButton" onClick={e => setPage(2)}>Vahvista Tilaus</button>
            {filteredProducts.map(c => <Purchase key={c.id} product={c} />)}
            <p>{calcTotal()}€</p>
        </div>
    )
}
export { Cart, Purchase };